* {
  box-sizing: border-box;
  color: black;
}

body {
  font-family: Poppins, -apple-system, Roboto, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.2em;
}

header {
  width: 100%;
  position: fixed;
  display: flex;
  height: 80px;
  align-items: center;
  background: #fef9ef;
  justify-content: space-between;
  padding: 0 100px;
}

nav ul li {
  display: inline-block;
  padding: 0 30px;
}

nav ul li a {
  text-decoration: none;
}

nav ul li a:hover {
  color: #a2d2ff;
}

.wrapper {
  margin: 0 auto;
  max-width: 1270px;
  padding: 80px 0;
}

#home {
  padding-top: 8rem;
  background: #fef9ef;
}

.logo img {
  width: 70px;
}

.home-wrapper {
  text-align: center;
  display: flex;
  width: 100%;
}

.home-container {
  padding: 0 50px;
}

.home-container-img {
  width: 100%;
}

.right-home-container {
  text-align: left;
}

.tagline {
  font-size: 3.1em;
  line-height: 1.3;
  margin-bottom: 50px;
}

.tagline-p {
  font-size: 1.1rem;
}

#submit {
  height: 2rem;
  background-clip: padding-box;
  border-radius: 3rem;
  width: 10rem;
  border: none;
  text-transform: uppercase;
  display: block;
  font-family: inherit;
  font-size: 1rem;
  font-weight: bold;
  background-color: #5584ac;
  color: white;
}

#submit:hover {
  background-color: #a2d2ff;
  cursor: pointer;
}

#email {
  height: 2.8rem;
  background-clip: padding-box;
  border-radius: 0.3rem;
  width: 350px;
  border: none;
  padding: 1rem;
  font-size: inherit;
  margin: 0.5rem 0;
}

input,
textarea {
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

input:focus,
textarea:focus {
  border-color: #fef9ef;
  outline: 0;
  box-shadow: 0 0 0 0.2rem #a2d2ff;
}

#services {
  text-align: center;
  background: #5584ac;
}

.services-wrapper {
  display: flex;
  margin: 50px 0;
  justify-content: space-between;
}

.box {
  margin: 0 20px;
  width: 100%;
  padding: 3rem 3rem;
  background-color: #fef9ef;
  border-radius: 1rem;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

.box h4 {
  text-align: center;
}

.box p {
  text-align: left;
}

h2 {
  padding-bottom: 1rem;
  font-size: 3rem;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
}

h4 {
  margin: 0 0 30px 0;
  text-transform: uppercase;
}

p {
  line-height: 1.6rem;
}

.box svg {
  text-align: center;
  width: 3rem;
  margin: 10px;
}

.whyus-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#whyus {
  background: #fef9ef;
}

.whyus-box {
  margin: 0 20px;
}

.whyus-box p {
  text-align: left;
}

#contact {
  background: #5584ac;
}

.contact-wrapper {
  text-align: center;
}

.messageform-wrapper {
  max-width: 800px;
  margin: 50px auto;
  padding: 10px;
}

#message-fullname,
#message-email,
#message-subject,
#message-body {
  height: 2.8rem;
  background-clip: padding-box;
  border-radius: 0.3rem;
  border: none;
  padding: 1rem;
  font-family: inherit;
  font-size: inherit;
  margin: 0.5rem 0;
  width: 100%;
}

.line1 {
  display: flex;
  gap: 10px;
}

#message-submit {
  height: 2.8rem;
  border-radius: 0.3rem;
  width: 12rem;
  border: none;
  display: block;
  font-family: inherit;
  font-size: 1rem;
  font-weight: bold;
  margin-top: 1rem;
}

#message-submit:hover {
  background-color: #fef9ef;
  cursor: pointer;
}

#message-body {
  min-height: 150px;
  width: 100%;
  padding: 0.625rem;
  resize: none;
}

footer {
  font-size: 0.9rem;
  text-align: center;
  height: 30px;
  background: #5584ac;
}

.bars-button {
  display: none;
  text-align: center;
  font-size: 2rem;
  float: right;
  margin-right: 15px;
  margin-top: 15px;
  cursor: pointer;
}

#bars {
  display: none;
}

@media (max-width: 1000px) {
  .services-wrapper {
    display: block;
    width: 90%;
    margin: 50px auto;
  }

  .box {
    margin: 30px auto;
    width: 100%;
    padding: 1.5rem;
  }

  .whyus-wrapper {
    display: block;
  }

  .home-wrapper {
    text-align: center;
    display: block;
    width: 100%;
  }

  .home-container {
    padding: 0;
  }

  header {
    width: 100%;
    position: fixed;
    padding: 0 3rem;
  }

  .right-home-container {
    text-align: center;
  }

  #submit {
    display: block;
    margin: 0 auto;
  }

  iframe {
    max-width: 100%;
    padding-top: 20px;
  }

  .wrapper {
    padding: 40px 0;
  }

  .whyus-box {
    text-align: center;
  }

  .tagline {
    font-size: 2.5em;
  }

  #email {
    width: 70%;
  }

  nav ul li a {
    color: #fef9ef;
  }

  .bars-button {
    display: block;
    float: right;
  }

  nav ul li a:hover {
    color: #a2d2ff;
  }

  ul {
    position: fixed;
    top: 80px;
    left: 100%;
    text-align: center;
    width: 100%;
    background: #5584ac;
  }

  nav ul li {
    display: block;
    margin: 2rem 0;
  }

  #bars:checked ~ ul {
    left: 0;
  }
}